import React from 'react';
import axios from 'axios'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import PhoneIcon from '@material-ui/icons/Phone';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';		
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';	
import StarRateIcon from '@material-ui/icons/StarRate';		

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button'

function TabPanel(props) {
  const { item, children, value, index, ...other } = props;
  const styles = {
  	  width:'100%',
  	  height:'100%',
	  media: {
	    height: 0,
	    paddingTop: '56.25%', // 16:9
	  },
	  expand: {
	    transform: 'rotate(0deg)',
	    marginLeft: 'auto',
	  },
	  expandOpen: {
	    transform: 'rotate(180deg)',
	  },
	  avatar: {
	    backgroundColor: red[500],
	  },
  }
  return (
    <Card
    	role="tabpanel"
    	hidden={value !== index}
    	id={`simple-tabpanel-${index}`}
    	aria-labelledby={`simple-tab-${index}`}
    	style={styles.root}
    >
      <CardHeader
        title={item.name}
        subheader={
        	<div>
        		<a target="_blank" href={item.maps_url}>{item.address}
        		</a>
        		<div style={{display:'flex',alignItems:'center',justifyContent:'center'}}>
        			<PhoneIcon />
        			<span style={{margin:'5px'}}>{item.phone_number}</span>
        		</div>
        	</div>
        }
      />
      <CardMedia
        style={styles.media}
        image={item.photo_url}
      />
      <CardContent>
      	<div style={{display:'flex',width:'100%',marginBottom:'10px'}}>
      		<div style={{display:'flex', flex:1, alignItems:'center', justifyContent:'center'}}>
      			Price: 
      			{
      				item.price_level != -1 ? 
      				new Array(Number(item.price_level)).fill(0).map((item,index)=>{
      					return <AttachMoneyIcon />
      				}) : ' No price information '
      			}
      		</div>
      		<div style={{display:'flex', flex:1, alignItems:'center',justifyContent:'center'}}>
      			{item.rating  != -1 ? item.rating : 'No rating available'}
      			<StarRateIcon />
      		</div>
      	</div>
      	{
      		item.opening_hours.map((value,index)=>{
      			return(
			        <Typography variant="body2" color="textSecondary" component="p">
			        	{value}
			        </Typography>
      			)
      		})
      	}
        <div style={{marginTop:'10px'}}/>
        {
          item.routes.map((route,index)=>{
            return (
              <Typography variant="body2" color="textSecondary" component="p">
                Travel duration from {route.origin}: {route.journey_length}
              </Typography>
            )
          })
        }
      </CardContent>
    </Card>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function Share(props) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const [open, setOpen] = React.useState(false);
  const [url, setUrl] = React.useState("");

  const handleChange = (event, newValue) => {
  	const newMapPos = {
  		lat: props.results[newValue].lat,
  		lng: props.results[newValue].lng
  	}
  	props.tabClicked(newMapPos);
    setValue(newValue);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleCopy = () => {
	navigator.clipboard.writeText(url).then(function(yay) {
	  /* clipboard successfully set */
	  alert("Copied to cliipboard!");
	}, function(err) {
	  /* clipboard write failed */
	  console.log(err)
	});
  }

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Tabs TabIndicatorProps={{style:{
          backgroundColor:'white'
        }}} value={value} onChange={handleChange} aria-label="simple tabs example">
          <Tab label="Result 1" {...a11yProps(0)} />
          <Tab label="Result 2" {...a11yProps(1)} />
          <Tab label="Result 3" {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      {
      	props.results.map((item, index)=>{
	      	return (
	      		<TabPanel item={item} value={value} index={index}/>
	      	)
      	})
      }
      {
        window.location.href.includes('share') ? null :
        <IconButton
          onClick={()=>{
          axios.post('/generate-share-link', {
            data: JSON.stringify({
              nearest_places: props.results,
            })
        })
        .then(response => {
          setUrl(window.location.origin + "/share/" + response.data)
          handleClickOpen();
        })
        .catch(function (error) {
          console.log(error);
        });
          }}
        >
          <ShareIcon />
        </IconButton>      
      }

		<Dialog
			open={open}
			onClose={handleClose}
			aria-labelledby="alert-dialog-title"
			aria-describedby="alert-dialog-description"
		>
			<DialogTitle id="alert-dialog-title">{"Share the link below with your friends!"}</DialogTitle>
			<DialogContent>
			  <DialogContentText id="alert-dialog-description">
			    {url}
			  </DialogContentText>
			</DialogContent>
			<DialogActions>
			  <Button onClick={handleClose} color="primary">
			    Close
			  </Button>
			  <Button onClick={handleCopy} color="primary" autoFocus>
			    Copy to clipboard
			  </Button>
			</DialogActions>
		</Dialog>
    </div>
  );
}
