import React from 'react';
import logo from './logo.svg';
import './App.css';

import Grid from '@material-ui/core/Grid';

//Components
import NavBar from './components/Header.js'
import LocationInput from './components/LocationInputs.js'
import Map from './components/Map.js'
import Share from './components/Share.js'

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

class App extends React.Component {
  state = {
    results:[],
    inputResults:[],
    newMapPos: {
      lat:0,
      lng:0
    }
  }
  locationsReceived = (locations) => {
    console.log(locations[0])
    const inputLatLngs = locations[0].routes.map(route=>{
      return {
        lat: route.origin_lat,
        lng: route.origin_lng,
        text: route.origin
      }
    });
    this.setState({
      results: locations,
      inputResults: inputLatLngs
    })
  }
  tabClicked = (pos) => {
    this.setState({
      newMapPos: pos
    })
  }
  render(){
    return (
        <Router>
          <div>
            <NavBar style={{position:'fixed'}}/>
            <Switch>
              <Route path="/share/:id">
                <div>
                  <Share/>
                </div>
              </Route>
              <Route exact path="/">
                <div className="App">
                  <div style={{display:'flex'}}>
                    <div style={{flex:4, height:'100%', overflowY: 'scroll'}}>
                      <LocationInput tabClicked={this.tabClicked} locationsReceived={this.locationsReceived}/>
                    </div>
                    <div style={{flex:8}}>
                      <Map tabClickPosition={this.state.newMapPos} inputResults={this.state.inputResults} results={this.state.results} />
                    </div>
                  </div>
                </div>
              </Route>
              <Route>
                <div className="App" style={{height:'calc(100vh - 64px)',display:'flex',alignItems:'center',justifyContent:'center'}}>
                  <h1>Sorry, this page unfortunately does not exist (๑◕︵◕๑)</h1>
                </div>
              </Route>
            </Switch>
          </div>
        </Router>
    );    
  }
}

export default App;
