import React from 'react';
// import { Map, GoogleApiWrapper , Marker} from 'google-maps-react';
import GoogleMapReact from 'google-map-react';

const Marker = ({ text , color, type}) => {
	return (
		<div>
	        <div
	          className="pin bounce"
	          style={{ backgroundColor: color, cursor: 'pointer' }}
	          // onClick={()=>{alert('lOL')}}
	        >
	        </div>
	        {
	        	type == 'result' ? (
			        <div className="marker-no bounce-no-rotate">
			        	{text}
			        </div>
	        	) : (
			        <div className="marker-name bounce-no-rotate">
			        	{text}
			        </div>
	        	)
	        }
	        <div className="pulse" />
      	</div>
    )
}
class SimpleMap extends React.Component {
	state = {
		center:{
	      lat: 51.498818,
	      lng: -0.174880
		},
		zoom: 16
	}

	componentDidUpdate(prevProps) {
	  if(prevProps.tabClickPosition.lat != this.props.tabClickPosition.lat){
	  		this.setState({
	  			center: this.props.tabClickPosition,
	  			zoom:17
	  		})
	  } else if(prevProps.results.length !== this.props.results.length){
		  const locationsArray = [...this.props.results,...this.props.inputResults];
		  console.log(locationsArray);
		  if (this.props.results.length !== prevProps.results.length) {
			var maxLat = -360
			var minLat = 360
			var maxLng = -360
			var minLng = 360
			var midLat = 0
			var midLng = 0;
			var difLat, difLng;
			var dif, zoom;
			for (var i = 0; i < locationsArray.length; i++) {
			  maxLat = Math.max(maxLat, locationsArray[i].lat);
			  minLat = Math.min(minLat, locationsArray[i].lat);
			  maxLng = Math.max(maxLng, locationsArray[i].lng);
			  minLng = Math.min(minLng, locationsArray[i].lng);
			}
			midLat = (maxLat + minLat) / 2;
			midLng = (maxLng + minLng) / 2;
			difLat = maxLat - minLat;
			difLng = maxLng - minLng;
			dif = Math.abs(Math.max(difLat, difLng) / 360 * 40075016);
			if (dif < 1128) zoom = 16;
			else if (dif < 2256) zoom = 16;
			else if (dif < 4513) zoom = 15;
			else if (dif < 9027) zoom = 14;
			else if (dif < 18055) zoom = 13;
			else if (dif < 36111) zoom = 12;
			else if (dif < 72223) zoom = 11;
			else if (dif < 144447) zoom = 10;
			else if (dif < 288895) zoom = 9;
			else if (dif < 577790) zoom = 8;
			else if (dif < 1155581) zoom = 7;
			else if (dif < 2311162) zoom = 6;
			else if (dif < 4622324) zoom = 5;
			else if (dif < 9244649) zoom = 4;
			else zoom = 3;
			this.setState({
				center: {
					lat:midLat,
					lng:midLng
				},
				zoom
			})	  	
	  }

	  }
	}

  render() {
    return (
      // Important! Always set the container height explicitly
      <div style={{ height: 'calc(100vh - 64px)', width: '100%'}}>
        <GoogleMapReact
          bootstrapURLKeys={{ key:'AIzaSyDRjXSw6KA9jg1WBA--alzfabOCCQ6oMOI'}}
          defaultCenter={this.props.center}
          center={this.state.center}
          defaultZoom={this.state.zoom}
          zoom={this.state.zoom}
          onGoogleApiLoaded={this.mapLoaded}
          heatmapLibrary={true}
        >
	        {
	        	this.props.results.map((item,index)=>{
				  return(<Marker
				  	key={'result-marker'+index}
				    lat={item.lat}
				    lng={item.lng}
				    text={index+1}
				    color="red"
				    type="result"
				  />)
	        	})
	        }
	        {
	        	this.props.inputResults.map((item,index)=>{
				  return(<Marker
				  	key={'input-marker'+index}
				    lat={item.lat}
				    lng={item.lng}
				    text={item.text}
				    color="blue"
				    type="input"
				  />)
	        	})
	        }
        </GoogleMapReact>
      </div>
    );
  }
}

export default SimpleMap;