import React from 'react';

import axios from 'axios'
import Map from './Map.js'
import Results from './Results.js'

export default class Test extends React.Component {
	state = {
		inputResults: [],
		nearestPlaces: [],
		newMapPos: {
			lat:0,
			lng:0
		}
	}
	componentDidMount(){
		const shareId = window.location.pathname.replace('/share/','')
		axios.post('/get-share',{
			data: JSON.stringify({ id: shareId})
		})
		  .then(response=> {
		    const inputLatLngs = response.data.nearest_places[0].routes.map(route=>{
		      return {
		        lat: route.origin_lat,
		        lng: route.origin_lng,
		        text: route.origin
		      }
		    });
		    this.setState({
		    	nearestPlaces: response.data.nearest_places,
		    	inputResults: inputLatLngs
		    })
		  })
		  .catch(function (error) {
		    console.log(error);
		  });
	}
	  tabClicked = (pos) => {
	    this.setState({
	      newMapPos: pos
	    })
	  }
	render(){
		return(
	        <div className="App">
	          <div style={{display:'flex'}}>
	            <div style={{flex:4, height:'100%', overflowY: 'scroll'}}>
	              <Results tabClicked={this.tabClicked} results={this.state.nearestPlaces}/>
	            </div>
	            <div style={{flex:8}}>
	              <Map tabClickPosition={this.state.newMapPos} inputResults={this.state.inputResults} results={this.state.nearestPlaces} />
	            </div>
	          </div>
	        </div>
		)
	}
}