import React from 'react';
import axios from 'axios';
import 'date-fns';

import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Icon from '@material-ui/core/Icon';
import AddIcon from '@material-ui/icons/Add';
import Button from '@material-ui/core/Button';
import SendIcon from '@material-ui/icons/Send'
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import DateFnsUtils from '@date-io/date-fns';
import CircularProgress from '@material-ui/core/CircularProgress';

import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';

import Results from './Results.js'

export default class LocationInputs extends React.Component {
	state = {
		showInput: true,
		locations:[""],
		category: 'Restaurants',
		selectedDate: new Date('2014-08-18T21:11:54'),
		loading: false
	}

	changeText = (event, index) => {
		const { value } = event.target;
    const locations = [...this.state.locations];
    locations[index] = value;
		this.setState({
			locations
		})
	}

	addLocationField = () => {
		this.setState({
			locations: [...this.state.locations, ""]
		})
	}

	removeLocation = (index) => {
		let locations = this.state.locations;
		locations.splice(index, 1);
		this.setState({
			locations
		})
	}

	selectCategory = (event) => {
		const { value } = event.target;
		this.setState({
			category: value
		})
	}

	postLocationInput = () => {
		const checkAllEmpty = () => {
			const {locations} = this.state;
			for(let i = 0; i < locations.length; i++){
				console.log(locations[i] == "")
				if(locations[i] != "") return true;
			}
			return false;
		}
		if(!checkAllEmpty()){
			alert('Please make sure your input is not empty!')
		} else {
			this.setState({
				loading:true
			})
			let { locations } = this.state;
			locations = locations.filter(location => location != "")
			axios.post('/post-location-inputs', {
					data: JSON.stringify({
						locations: locations,
						category: this.state.category
					})
			  })
			  .then(response => {
			    this.setState({
			    	showInput: false,
			    	results: response.data,
			    	loading:false
			    })
			    this.props.locationsReceived(response.data)
			  })
			  .catch( (error) => {
					this.setState({
						loading:false
					})
			  });			
			}
	}

	render(){
		return(
			<div>
				{
					this.state.loading ? <CircularProgress style={{width:'100px',height:'100px',marginTop:'65%'}}/>
					: this.state.showInput ? 
					(
						<div style={{padding:'40px'}}>
				      <FormControl style={{float:'left',marginBottom:'20px'}}>
				        <InputLabel shrink id="demo-simple-select-placeholder-label-label">
				          Category
				        </InputLabel>
				        <Select
				          labelId="demo-simple-select-placeholder-label-label"
				          id="demo-simple-select-placeholder-label"
				          value={this.state.category}
				          onChange={this.selectCategory}
				          displayEmpty
				        >
				          <MenuItem value={'Restaurants'}>Restaurants</MenuItem>
				          <MenuItem value={'Shopping'}>Shopping</MenuItem>
				          <MenuItem value={'Things to do'}>Things to do</MenuItem>
				        </Select>
				      </FormControl>
					    <form noValidate autoComplete="off">
						    {
						    	this.state.locations.map((val,index)=>{
						    		return (
						    			<div key={index} style={{marginBottom:'20px', justifyContent:'center', alignItems:'center', width:'100%',display:'flex'}}>
							    			<TextField 
							    				style={{width:'100%', paddingRight:'20px'}} 
							    				value={this.state.locations.index}
							    				label={'Location ' + (index + 1)} 
							    				onChange={(event)=>{this.changeText(event,index)}}
							    				variant="outlined" 
							    			/>
								        <IconButton 
								        	onClick={()=>{this.removeLocation(index)}} 
								        	style={{ boxShadow: '0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)',
								        		color: 'white', 
								        		backgroundColor:'red',
								        		width:'48px',
								        		height:'48px'
								        	}} 
								        	aria-label="delete">
								          <DeleteIcon fontSize="small" />
								        </IconButton>
						    			</div>
						    		)
						    	})
						    }
					    </form>
							<Button
								variant="contained"
								color="primary"
								size="large"
								style={{width:'100%', marginBottom:'20px'}}
								onClick={this.addLocationField}
								startIcon={<AddIcon />}
							>
								Add location
							</Button>
							<Button
								variant="contained"
								color="primary"
								size="large"
								style={{width:'100%', marginBottom:'20px'}}
								onClick={this.postLocationInput}
								endIcon={<SendIcon />}
							>
								Find me a place
							</Button>
						</div>
					) : <Results tabClicked={this.props.tabClicked} results={this.state.results}/>
				}
		  </div>
		)
	}
}